.modal {
  height: inherit;
  width: 100%;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.overlay {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1;
  backdrop-filter: blur(1px);
}
.Center_box {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}
.Image1 {
  z-index: 10;
  width: 80%;
  align-items: center;
  display: flex;
  justify-content: center;
  object-fit: none;
  height: 100%;
  top: 20%;
}

.content {
  background-color: white;
  z-index: 2;
  padding: 32px;
  box-shadow: 0 1px 12px rgba(0, 0, 0, 0.5);
  display: flex;
  flex-direction: column;
  border-radius: 8px;
}

.content > button {
  margin: 12px;
}

.Close_Image {
  fill: white;
  display: flex;
  align-items: center;
  justify-content: end;
  height: 45%;
  position: absolute;
  z-index: 11;
  right: 0%;
 }
@media only screen and (max-width: 500px) {
  .Image1 {
    z-index: 10;
    width: 90%;
    align-items: center;
    display: flex;
    justify-content: center;
    object-fit: contain;
    height: 100%;
    top: 20%;
  }  
 }